<template>
	<div class="position-relative" style="z-index: 1;">
		<transition name="fade">
		    <tip v-if="!show" :tipMessage="tipMessage"></tip>
		</transition>
		<div style="margin: 30px 0px;">
			<el-upload
			  class="upload-demo"
			  ref="upload"
			  action="/api/upLoadImg"
			  :on-preview="handlePreview"
			  :on-remove="handleRemove"
			  :on-change="onChange"
			  :file-list="fileList"
			  :limit="1"
			  :auto-upload="false">
			  <el-button @click="sub()" slot="trigger" size="small" type="primary" :disabled="!disabled">选取文件</el-button>
			  <!-- <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button> -->
			  <div slot="tip" class="el-upload__tip">只能上传一张jpg/png文件，且不超过500kb</div>
			</el-upload>
		</div>
		<div style="width: 500px;">
			
			<el-form :label-position="labelPosition" label-width="80px" :model="formLabelAlign">
			  <el-form-item label="汽车图片标题">
			    <el-input v-model="obj.title"></el-input>
			  </el-form-item>
			  <el-form-item label="汽车名称">
			    <el-input v-model="obj.carName"></el-input>
			  </el-form-item>
			  <el-form-item label="代驾价格">
			    <el-input v-model="obj.drivingPrices"></el-input>
			  </el-form-item>
			  <el-form-item label="餐费">
			    <el-input v-model="obj.meals"></el-input>
			  </el-form-item>
			  <el-form-item label="汽车颜色">
			    <el-input v-model="obj.color"></el-input>
			  </el-form-item>
			  <el-form-item label="汽车品牌">
			    <el-input v-model="obj.brand"></el-input>
			  </el-form-item>
			  <el-form-item label="乘坐的人数">
			    <el-input v-model="obj.people"></el-input>
			  </el-form-item>
			</el-form>
		</div>
			
		<p style="color: orangered;font-size: 20px;font-weight: bolder;">以下填空请记得写单位</p>
		
		<div>
			
			<div class="item" style="display: flex;padding: 20px;">
				市内日租 (基本价格)：<input type="text" v-model="obj.infoaa">
				市内日租 (核定公里数)：<input type="text" v-model="obj.infoab">
				市内日租 (超程单价)：<input type="text" v-model="obj.infoac">
				市内日租 (核定小时)：<input type="text" v-model="obj.infoad">
				市内日租 (超时单价)：<input type="text" v-model="obj.infoae">
			</div>
			<div class="item" style="display: flex;padding: 20px;">
				深圳站接送 (基本价格)：<input type="text" v-model="obj.infoba">
				市内日租 (核定公里数)：<input type="text" v-model="obj.infobb">
				市内日租 (超程单价)：<input type="text" v-model="obj.infobc">
				市内日租 (核定小时)：<input type="text" v-model="obj.infobd">
				市内日租 (超时单价)：<input type="text" v-model="obj.infobe">
			</div>
			<div class="item" style="display: flex;padding: 20px;">
				深圳宝安机场接送 (基本价格)：<input type="text" v-model="obj.infoca">
				市内日租 (核定公里数)：<input type="text" v-model="obj.infocb">
				市内日租 (超程单价)：<input type="text" v-model="obj.infocc">
				市内日租 (核定小时)：<input type="text" v-model="obj.infocd">
				市内日租 (超时单价)：<input type="text" v-model="obj.infoce">
			</div>
		</div>
		<button style="width: 100%;height: 40px; background-color: #FFD04B;outline: none;" :plain="true" @click="commit()">提交</button>
	</div>
</template>



<script>
	const querystring = require('querystring');
	import tip from "../tip.vue"
	export default {
		data() {
			return {
				obj:{
					title: null,
					carName: null,
					drivingPrices:null,
					meals:null,
					people: null,
					color:null,
					brand:null,
					infoaa: null,
					infoab: null,
					infoac: null,
					infoad: null,
					infoae: null,
					infoba: null,
					infobb: null,
					infobc: null,
					infobd: null,
					infobe: null,
					infoca: null,
					infocb: null,
					infocc: null,
					infocd: null,
					infoce: null,
				},
				image:null,
				fileList: [],
				dataImg:[],
				disabled:true,
				show: true,
				tipMessage:"添加成功",
				labelPosition: 'right',
				formLabelAlign: {
				  name: '',
				  region: '',
				  type: ''
				}
			}
		},
		components: {
			tip
		},
	
		methods: {
			
			sub(){
				if(this.$refs.upload.uploadFiles.length>=0){
					this.disabled = false
					// alert("只能上传一张图片")
				}
			},
			
			
			handleRemove(file, fileList) {
				console.log(file, fileList);
				if(this.$refs.upload.uploadFiles.length===0){
					this.disabled = true
				}
			},
		    handlePreview(file) {
				console.log(file);
		    },
			onChange(file) {
				var inp1 = document.querySelector("[name=file]")
				this.dataImg = inp1.files
			},
			
			async commit() {
				
				for(let key in this.obj){
					if(this.obj[key] == null){
						console.log("zheshi"+ key +this.obj[key])
						this.$message({
							 message: '注意！，输入框不能为空',
							 type: 'warning'
						});
						return;
					}
				}
				let that = this;
				var fromDate = new FormData();
				for (const file of this.dataImg) {
					fromDate.append("image", file, file.name)
				}
				await fetch("/api/upLoadImg", {
						body: fromDate,
						method: "POST"
					})
					.then((resp) => resp.json())
					.then(res => {
						this.image = res.data;
						this.obj["image"] = this.image[0]
						console.log(this.obj)
					})
					.catch(
						err => {
							alert("请传入图片/限制12张图片")
						}
					)
				this.$A.post("/api/insertCarInfo",querystring.stringify(this.obj)).then(res =>{
					console.log(res)
					  this.show=false
					  setTimeout(()=>{
						this.show=true
					  },3000)
				}).catch((error)=>{
						console.log(error)
					}
				)
				
			},
		},
		mounted() {

		},
		watch: {

		},

	}
</script>

<style>
	
	ul>.el-upload-list__item{
		width: 500px;
		background: linear-gradient(0deg, #0C7AEB 0%, #35D1FD 100%);
	}
	.item>input{
		width: 300px;
		border-radius: 5px;
		
	}


</style>
